import { gsap } from "gsap";

const titleDistanceY = -30;

function transitionEnter(elem , onFinish){
    
    const timeline = gsap.timeline({
        repeat: 0 ,
        onComplete : onFinish
    });

    const delay = 1;
    const parent = elem.getElementsByClassName("p-loader")[0];
    const cubes = parent.getElementsByClassName("boxes")[0];
    const title = parent.getElementsByClassName("loading-title")[0];
    timeline.set(cubes , {scale:1});
    timeline.set(parent , {autoAlpha : 1});
    

    timeline.to(title, {
        y: titleDistanceY,
        autoAlpha : 0,
        duration: .5,
        ease : "back.in(1.7)",
    } , "+="+delay);

    timeline.to(parent, {
        autoAlpha: 0,
        duration: .8,
    } )

}

function transitionLeave(elem , done) {
    
    const delay = .3; // useful for showing active links transition
    const parent = elem.getElementsByClassName("p-loader")[0];
    const cubes = parent.getElementsByClassName("boxes")[0];
    const title = parent.getElementsByClassName("loading-title")[0];

    const timeline = gsap.timeline({
        repeat: 0
    });

    timeline.set(cubes , { scale:0});
    timeline.set(parent , {autoAlpha : 0});

    timeline.to(parent, {
        autoAlpha: 1,
        duration: .3,
        onComplete: () => {
            _animateCubesIn(cubes, done);
        }
    } , "+="+delay);

    timeline.to(title, {
        y: 'inherit',
        autoAlpha : 1,
        duration: .5,
        ease : "back.out(1.7)",
    } , "-=.1");
}


function _animateCubesIn(cubes , onFinish){
    gsap.to(cubes, {
        scale: 1,
        duration: .4,
        ease: "back.out(4)",
        onComplete : onFinish
    });
    
}




export {transitionEnter , transitionLeave}